import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustAssociationRelationComponent } from "./cust-association-relation.component";
import { PersonalRelationComponent } from "./personal-relation/personal-relation.component";
export const routes: Routes = [
    {
        path: '', component: CustAssociationRelationComponent,
        children: [
            { path: '', redirectTo: 'personal-relation',pathMatch: 'full' },
            { path: 'personal-relation', component: PersonalRelationComponent },
        ]
    }
];
export const CustAssociationRelationRouting: ModuleWithProviders = RouterModule.forChild(routes);