  //内部员工关系查询
  userRelation() {
    this.userRelationBean.clientNo = this.custNo;
    // this.userRelationBean.clientNo = '1000160008';
    this.cusViewHttpService.BZ_1300300001801_LOAN(this.userRelationBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.queryResultArray;
        this.items.forEach(item => {
          this.httpService.queryTellerById({ tellerId: item.appUserId }).subscribe(data => {
            if (data.tellerName != null) {
              item.appUserId = data.tellerName;
            }
          });
          this.httpService.queryOrgById({ orgId: item.appBranchNo }).subscribe(data => {
            if (data.orgName != null) {
              item.appBranchNo = data.orgName;
            }
          });
        });
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }

