  //添加的回调

  addCall(param) {
    console.log(param)
    if (param == false) {
      this.display = false;
      this.selectPartyRelationshipBean.pageNum = 1;
      this.query();
      if (param != '取消') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: '添加成功' }];
      }
    } else {
      console.log(param);
      this.display = false;
      this.outVal = param.outVal;
      this.aaaa = param.aaaa;
    }
  }

  //修改按钮事件
  modifyClick(item, Name, No) {
    item.custNo = this.custNo;
    item.parentTypeId = No;
    item.parentTypeName = Name;
    this.headerTitle = '修改';
    this.updateDisplay = true;
    this.updateValue = _.clone(item);
    document.body.style.overflow = 'hidden';
  }

