  deleteErrorData(car) {
    let param = {
      custNo: this.custNo,
      relationshipCustNo: car.relationshipCustNo,
      relationshipType: car.relationshipType
    };
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        this.httpService.deleteErrorData(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.query();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
          }
        });
      }
    });
  }


